
import cookie from 'vux/src/tools/cookie/index.js'

import api from '../../assets/js/api'
import {getParams, weChatPay} from '../../assets/js/util'
export default {
components: {},
data () {
return {
code: getParams()['code'] || '',
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(text){
this.$vux.loading.show({
text: text || '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
login(){
this.showLoading('正在登录')
this.$http.post(api.wxLogin, {
wx_code: this.code
}).then(res => {
this.hideLoading()

if(res.data.openId){
cookie.set('my_openId',res.data.openId, {
expires: 30
})
}
this.$router.replace({
path: '/tixian'
})
}).catch( e => {
this.hideLoading();
})
}
},
mounted() {

document.getElementsByTagName('title')[0].textContent = '正在登录';
this.login()
}
}



